.footer {
  width: 100%;
  padding: 8vh 0 3vh;
  margin-top: 5vh;
}
.container {
  width: fit-content;
  margin: auto;
}
.paragraph {
  width: 42vw;
  font-size: 2.5em;
  margin: auto;
  font-weight: 650;
}
.paragraph span {
  color: #fdd807 ;
}
.contactOptions {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contactOptions i {
  font-size: 40px;
 padding: 25px 35px;
  transition: 0.5s;
}

.contactOptions i:hover {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
  color: #fdd807;;

}
.contactHeading
{
  font-size: x-large;
  font-weight: 600;
}



@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}







.btn
{
font-family: "Montserrat", Arial, Helvetica, sans-serif;
background-color:#fdd807;
padding: 8px 25px;
border: none;
cursor: pointer;
font-size: 18px;
font-weight: 650;
border-radius: 10px;
margin: 20px 0;
}
.form
{
  width: 60%;
  margin: auto;
  margin-top: 5%;
 background-color:#fdd807;
  border-radius: 6%;
  border: 1px solid ;
  margin-bottom: 30px;
}

[type="submit"],[type="back"] {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  width:30%;
  margin: 0px 40px;
  color:#fdd807 ;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  background-color: green;
  font-size: 16px; 
  padding: 10px 0px;
  transition: all 0.3s;
  margin-top: 25px;
  font-weight: 550;
}
[type="back"]
{
  background-color:red;
}
[type="back"]:hover,[type="submit"]:hover {
  background-color:black;
  color: white;
} 
form {
  max-width: 90%;
  margin: 40px auto;
}
.form input
{
  background-color: black;
}

.feedback_input {
  color: white;
  background-color: black;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 16px;
  border-radius: 5px;
  line-height: 25px;
  background-color: transparent;
  border: 2px solid #dbd2d2;
  transition: all 0.3s;
  padding: 10px;
  margin-bottom: 15px;
  width: 90%;
  box-sizing: border-box;
  outline: 0;
}
.bottom>div
{
  font-size: large;
  margin: 10px auto;
}
.bottom
{
  margin-top: 4vh;
}

.borderBottom {
  border-bottom: 3px solid #fdd807;
  width: 160px;
  margin: auto;
  margin-top: -12px;
  margin-bottom: 30px;
  /* margin: 20px auto; */
  /* border-radius: 10px; */
}



@media screen and (max-width: 1080px) {

  .paragraph {
    width: 50vw;
    font-size: 1.8em;
  }
  .contactOptions i {
    font-size: 35px;
    padding: 22px 30px;


  }
  .contactOptions {
    flex-wrap: wrap;
  }

  [type="submit"],[type="back"] {
    width:25%;
    margin: 0px 55px;
    font-size: 15px; 
    transition: all 0.3s;
    margin-top: 20px;
  }
  .feedback_input {
    font-size: 14px;
    line-height: 20px;
    padding: 10px;
    width: 100%;
  }

  .form
  {
    width: 65%;
    margin: auto;
    margin-top: 4%;
    border-radius: 25px;
    margin-bottom: 20px;
  }
  form {
    max-width: 85%;
    margin: 30px auto;
  }
  
  .contactHeading
  {
    font-size: x-large;
    font-weight: 600;
  }


  
.btn
{
font-size: 16px;
border-radius: 10px;
margin: 20px 0;
}
}


@media screen and (max-width: 800px) {

  .paragraph {
    width: 50vw;
    font-size: 1.6em;
    margin: auto;
    font-weight: 650;
  }
  .contactOptions i {
    font-size: 30px;
    padding: 18px 25px;


  }
  .contactOptions {
    flex-wrap: wrap;
  }

  [type="submit"],[type="back"] {
    width:26%;
    margin: 0px 35px;
    font-size: 12px; 
    margin-top: 10px;
  }

  
  .feedback_input {
    font-size: 14px;
    line-height: 20px;
    padding: 10px;
    width: 90%;
  }
  .form
  {
    width: 70%;
    margin: auto;
    margin-top: 4%;
    border-radius: 5%;
    margin-bottom: 20px;
  }

  form {
    max-width: 85%;
    margin: 15px auto;
  }
  
  .contactHeading
  {
    font-size: large;
    font-weight: 600;
  }

}

@media screen and (max-width: 500px) {
  .paragraph {
    width: 65vw;
    font-size: 1.2em;
    margin: auto;
  }
  

  .contactOptions i {
    font-size: 25px;
    padding: 15px;

  }
  .copyright {
    text-align: center;
  }

  [type="submit"],[type="back"] {
    width:20%;
    padding: 6px ;
    margin: 0px 25px;
    font-size: 10px; 
    margin-top: 8px;
  }


  .feedback_input {
    font-size: 10px;
    line-height: 12px;
    padding: 10px;
    margin-bottom: 10px;
  }
  .form
{
  width: 90%;
  margin: auto;
  margin-top: 5%;
  border-radius: 5%;
  margin-bottom: 20px;
}

form {
  max-width: 80%;
  margin: 10px auto;
}

.contactHeading
{
  font-size: 15px;
  font-weight: 600;
}


}
