.rootCont h1 {
	color: rgb(0, 49, 64);
  
	margin: 0 auto;
  }
  .rootCont > p {
	font-size: 30px;
	font-weight: 400;
	color: rgb(8, 111, 143);
	margin-top: 70px;
	/* margin: 10; */
  }
  .rootCont > p:nth-of-type(1) {
	font-size: 40px;
	font-weight: 400;
	color: black;
	/* margin: 50px; */
  }
  
  
  .stat{
	box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
	border: 1px solid gray.200;
	height: auto;
	width: auto;
	display: flex;
	gap:10px;
	padding: 20px;
	margin-bottom: 20px;
  }
  
  .stat>div{
	box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
	height: auto;
	width: 30%;
	padding: 3px;
	background-image: linear-gradient(to right, #cc2b5e  ,  #753a88);
	color: #fff;
  }
  .main :nth-child(1) {
	font-size : large;
	font-weight: bolder;
  }
  
  .main :nth-child(2) {
	font-size : large;
	font-weight: bolder;
  }
  
  /*   style={{ marginTop: "10px", marginBottom: "10px", height: "100px" }} */
  .git{
	margin-bottom: 30px;
	height: 250px;
  }
  
  .git>img{
	height: 100%;
	width: 100%;
  }
  
  .heading {
	width: fit-content;
	margin: auto;
	color: #8993b1;
	/* color:  #fdd807; */

	line-height: 40px;
}

.borderBottom {
	border-bottom: 3px solid #fdd807;
	width: 130px;
	margin: 10px auto;
	border-radius: 10px;
}
  
  @media only screen and (max-width: 1024px) {
	/* .rootCont {
	  height: 140vh;
	  padding-bottom: 5vh;
	} */
	.rootCont {
	  margin-top: 30px;
	  width: 90%;
	}
	.skillsContFront {
	  grid-template-columns: repeat(3, 33.3%);
	  /* height: 120vh; */
	}
  
  }
  
  